<script setup>
import {reactive} from "vue";
import {ArchiveOutline} from '@vicons/ionicons5'
import ReturnList from "@/components/ReturnList.vue";
import {useMessage} from 'naive-ui'
import {downloadXlsxTemplate} from "@/assets/js/api";
import config from '@/config'
const message = useMessage()
const siteUrl = config.host
const props = defineProps({
  show: {
    type: Boolean,
    default: false
  }
})

const data = reactive({
  token: localStorage.getItem('token'),
  showReturnList: false,
  returnList: []
})
const handleFinish = ({file, event}) => {
  if (event?.target?.response) {
    const res = JSON.parse(event.target.response)
    if (res.code === 200) {
      data.returnList = res.data
      data.showReturnList = true
      message.success("添加成功")
      emit('update:show', false)
      emit('finish')
    } else if (res.code === 400) {
      message.error(res.msg)
    } else if (res.code === 401) {
      message.warning(res.msg)
    }
  }
}

const emit = defineEmits(['update:show', 'finish'])

const downloadTemplate = () => {
  downloadXlsxTemplate().then(res => {
    window.open(`${siteUrl}/api/download/xlsx/template/`)
    message.success('模板下载成功')
  }).catch(err => {
    message.error('模板下载失败')
  })
}
</script>

<template>
  <n-modal v-model:show="props.show" @update:show="emit('update:show', $event)">
    <n-card style="width: 500px;" title="批量导入">
      <template #header-extra>
        <n-button size="small" type="info" @click="downloadTemplate">下载模板</n-button>
      </template>
      <n-upload :headers="{ 'Authorization': data.token }"
                :action="`${siteUrl}/api/admin/resource/add/multiple/`"
                directory-dnd
                max="1"
                @finish="handleFinish"
      >
        <n-upload-dragger>
          <div style="margin-bottom: 12px">
            <n-icon :depth="3" size="48">
              <ArchiveOutline/>
            </n-icon>
          </div>
          <n-text style="font-size: 16px">
            点击或者拖动文件到该区域来上传
          </n-text>
          <n-p depth="3" style="margin: 8px 0 0 0">
            请不要上传敏感数据，比如你的银行卡号和密码，信用卡号有效期和安全码
          </n-p>
        </n-upload-dragger>
      </n-upload>
    </n-card>
  </n-modal>
  <ReturnList :data="data.returnList" :show="data.showReturnList" @update:show="data.showReturnList = $event"/>
</template>

