<script setup>
const props = defineProps({
  show: {
    type: Boolean,
    default: false
  },
  data: {
    type: Array,
    default: () => {
      return []
    }
  }
})
const emit = defineEmits(['update:show'])
</script>

<template>
  <n-modal v-model:show="props.show" @update:show="emit('update:show', $event)">
    <n-card style="width: 500px;max-height: 500px;overflow: auto;" title="上传列表">
      <n-list style="max-height: 500px;overflow: auto;">
        <n-list-item v-for="(item, index) in props.data"
                     :key="index">
          <n-grid :cols="12" :x-gap="24" :y-gap="24" style="text-align: center">
            <n-grid-item :span="4">
              <n-text>{{ item.name }}</n-text>
            </n-grid-item>
            <n-grid-item :span="4">
              <n-progress
                  :percentage="item.percentage"
                  indicator-placement="inside"
                  status="default"
                  type="line"
              />
            </n-grid-item>
            <n-grid-item :span="4">
              <n-text v-if="item.status === 0" type="error">{{ item.msg }}</n-text>
              <n-text v-if="item.status === 1" type="success">{{ item.msg }}</n-text>
            </n-grid-item>
          </n-grid>
        </n-list-item>
      </n-list>
    </n-card>
  </n-modal>
</template>

<style scoped>

</style>