<script setup>

import {reactive} from "vue";
import {adminAddResource, adminGetCategory, adminGetTag} from "@/assets/js/api";
import {useMessage} from 'naive-ui'
import config from '@/config'
const siteUrl = config.host
// const siteUrl = ''
const message = useMessage()
const props = defineProps({
  show: {
    type: Boolean,
    default: false
  }
})

const data = reactive({
  categoryOptions: [],
  tagOptions: [],
  coverList: [],
  imageList: [],
  videoList: [],
  token: localStorage.getItem('token')
})

const formData = reactive({
  name: '',
  description: '',
  content: '',
  baiduUrl: '',
  tianyiUrl: '',
  aliUrl: '',
  active_code: '',
  status: true,
  category: [],
  tag: [],
  cover: [],
  image: [],
  video: []
})

const getCategoryTagOptions = () => {
  adminGetCategory({}).then(res => {
    if (res.data.code === 200) {
      data.categoryOptions = res.data.data.map(item => {
        return {
          label: item.name,
          value: item.id
        }
      })
      formData.category = res.data.data[0].id
    } else if (res.data.code === 400) {
      message.error(res.data.msg)
    } else if (res.data.code === 401) {
      message.warning(res.data.msg)
    }
  }).catch(err => {
    message.error(err.message)
  })
  adminGetTag({}).then(res => {
    if (res.data.code === 200) {
      data.tagOptions = res.data.data.map(item => {
        return {
          label: item.name,
          value: item.id
        }
      })
      formData.tag = res.data.data[0].id
    } else if (res.data.code === 400) {
      message.error(res.data.msg)
    } else if (res.data.code === 401) {
      message.warning(res.data.msg)
    }
  }).catch(err => {
    message.error(err.message)
  })
}
getCategoryTagOptions()


const emit = defineEmits(['update:show', 'finish'])

const handleCoverUpload = ({file, event}) => {
  if (event?.target?.response) {
    const res = JSON.parse(event.target.response)
    if (res.code === 200) {
      file.url = res.data.url
      file.id = res.data.id
      for (let i = 0; i < data.coverList.length; i++) {
        if (data.coverList[i].batchId === file.batchId && data.coverList[i].name === file.name) {
          data.coverList[i] = file
        }
      }
      message.success("上传成功")
    } else if (res.code === 400) {
      message.error(res.msg)
    } else if (res.code === 401) {
      message.warning(res.msg)
    }
  }
}

const handleImageUpload = ({file, event}) => {
  if (event?.target?.response) {
    const res = JSON.parse(event.target.response)
    if (res.code === 200) {
      file.url = res.data.url
      file.id = res.data.id
      for (let i = 0; i < data.imageList.length; i++) {
        if (data.imageList[i].batchId === file.batchId && data.imageList[i].name === file.name) {
          data.imageList[i] = file
        }
      }
      message.success("上传成功")
    } else if (res.code === 400) {
      message.error(res.msg)
    } else if (res.code === 401) {
      message.warning(res.msg)
    }
  }
}

const handleVideoUpload = ({file, event}) => {
  if (event?.target?.response) {
    const res = JSON.parse(event.target.response)
    if (res.code === 200) {
      file.url = res.data.url
      file.id = res.data.id
      for (let i = 0; i < data.videoList.length; i++) {
        if (data.videoList[i].batchId === file.batchId && data.imageList[i].name === file.name) {
          data.videoList[i] = file
        }
      }
      message.success("上传成功")
    } else if (res.code === 400) {
      message.error(res.msg)
    } else if (res.code === 401) {
      message.warning(res.msg)
    }
  }
}

const addResource = () => {
  if (formData.name === '') {
    message.error('请填写资源名')
  } else {
    formData.cover = data.coverList.map(item => {
      return item.coverId
    })
    formData.image = data.imageList.map(item => {
      return item.imageId
    })
    formData.video = data.videoList.map(item => {
      return item.videoId
    })

    adminAddResource(formData).then(res => {
      if (res.data.code === 200) {
        message.success("添加成功")
        emit('update:show', false)
        emit('finish')
        formData.cover = []
        formData.image = []
        formData.video = []
        formData.name = ''
        formData.description = ''
        formData.content = ''
        formData.baiduUrl = ''
        formData.tianyiUrl = ''
        formData.aliUrl = ''
        formData.active_code = ''
        formData.status = true
        formData.category = []
        formData.tag = []
        data.coverList = []
        data.imageList = []
        data.videoList = []
      } else if (res.data.code === 400) {
        message.error(res.data.msg)
      } else if (res.data.code === 401) {
        message.warning(res.data.msg)
      }
    }).catch(err => {
      message.error(err.message)
    })
  }
}
</script>

<template>
  <n-drawer :closable="false" :show="props.show" :width="700" @update:show="emit('update:show', $event)">
    <n-drawer-content title="添加资源">
      <n-scrollbar>
        <n-form ref="formRef" :model="formData" style="padding-right: 24px">
          <n-grid :cols="2" :x-gap="24" :y-gap="16">
            <n-grid-item :span="1">
              <n-form-item label="资源名称" path="name">
                <n-input v-model:value="formData.name" clearable placeholder="请输入资源名称"/>
              </n-form-item>
              <n-form-item label="资源描述" path="desc">
                <n-input v-model:value="formData.description" :rows="4" clearable placeholder="请输入资源描述"
                         type="textarea"/>
              </n-form-item>
              <n-form-item label="资源内容" path="desc">
                <n-input v-model:value="formData.content" :rows="4" clearable placeholder="请输入资源内容"
                         type="textarea"/>
              </n-form-item>
              <n-form-item label="百度云链接" path="url">
                <n-input v-model:value="formData.baiduUrl" clearable placeholder="请输入百度云链接"/>
              </n-form-item>
              <n-form-item label="天翼云链接" path="url">
                <n-input v-model:value="formData.tianyiUrl" clearable placeholder="请输入天翼云链接"/>
              </n-form-item>
              <n-form-item label="阿里云链接" path="url">
                <n-input v-model:value="formData.aliUrl" clearable placeholder="请输入阿里云链接"/>
              </n-form-item>
              <n-form-item label="激活码">
                <n-input v-model:value="formData.active_code" clearable placeholder="请输入激活码"/>
              </n-form-item>
              <n-form-item label="状态">
                <n-switch v-model:value="formData.status">
                  <template #checked>
                    启用
                  </template>
                  <template #unchecked>
                    禁用
                  </template>
                </n-switch>
              </n-form-item>
            </n-grid-item>
            <n-grid-item :span="1">
              <n-form-item label="资源分类">
                <n-select v-model:value="formData.category" :options="data.categoryOptions" clearable
                          placeholder="请选择资源分类"/>
              </n-form-item>
              <n-form-item label="资源标签">
                <n-select v-model:value="formData.tag" :options="data.tagOptions" clearable multiple
                          placeholder="请选择资源标签"/>
              </n-form-item>
              <n-form-item label="封面">
                <n-upload v-model:file-list="data.coverList"
                          :headers="{'Authorization': data.token}"
                          :action="`${siteUrl}/api/admin/cover/upload/`"
                          list-type="image-card"
                          max="1"
                          name="file"
                          @finish="handleCoverUpload"
                />
              </n-form-item>
              <n-form-item label="特色图片">
                <n-upload v-model:file-list="data.imageList"
                          :headers="{'Authorization': data.token}"
                          :action="`${siteUrl}/api/admin/image/upload/`"
                          list-type="image-card"
                          max="6"
                          multiple
                          name="file"
                          @finish="handleImageUpload"
                />
              </n-form-item>
              <n-form-item label="特色视频">
                <n-upload v-model:file-list="data.videoList"
                          :headers="{'Authorization': data.token}"
                          :action="`${siteUrl}/api/admin/video/upload/`"
                          list-type="image-card"
                          max="1"
                          name="file"
                          @finish="handleVideoUpload"
                />
              </n-form-item>
            </n-grid-item>
          </n-grid>
        </n-form>
      </n-scrollbar>
      <template #footer>
        <n-button secondary style="margin-right: 10px" @click="emit('update:show', false)">取消</n-button>
        <n-button secondary type="primary" @click="addResource">确定</n-button>
      </template>
    </n-drawer-content>
  </n-drawer>
</template>

<style scoped>
</style>