<script setup>
import AddResource from "@/components/AddResource.vue";
import {NButton, NIcon, useLoadingBar, useMessage, useModal} from "naive-ui";
import {h, reactive} from "vue";
import {
  CommentOutlined,
  FolderOutlined,
  HomeOutlined,
  InboxOutlined,
  SettingOutlined,
  TagOutlined,
  UserOutlined
} from "@vicons/antd";
import {useRouter} from "vue-router";
import {adminDeleteResource, adminGetResource} from "@/assets/js/api";
import EditResource from "@/components/EditResource.vue";
import AddResourceMultiple from "@/components/AddResourceMultiple.vue";

document.title = '资源';

const loadingBar = useLoadingBar()
const modal = useModal()
const message = useMessage()
const router = useRouter()
const renderIcon = (icon) => {
  return () => h(NIcon, null, {default: () => h(icon)});
}

const menuOptions = [
  {
    label: '首页',
    key: 'adminHome',
    icon: renderIcon(HomeOutlined),
  },
  {
    label: '资源',
    key: 'adminResource',
    icon: renderIcon(InboxOutlined),
  },
  {
    label: '分类',
    key: 'adminCategory',
    icon: renderIcon(FolderOutlined),
  },
  {
    label: '标签',
    key: 'adminTag',
    icon: renderIcon(TagOutlined),
  },
  {
    label: '评论',
    key: 'adminComment',
    icon: renderIcon(CommentOutlined),
  },
  {
    label: '用户',
    key: 'adminUser',
    icon: renderIcon(UserOutlined),
  },
  {
    label: '系统',
    key: 'adminSystem',
    icon: renderIcon(SettingOutlined),
  }
]

const handleSelect = (key) => {
  router.push({name: key})
}
const data = reactive({
  loading: false,
  showEditResource: false,
  showAddResource: false,
  showAddResourceMultiple: false,
  resourceData: [],
  editData: {},
})

const queryData = reactive({
  kwd: '',
  order: 'desc',
  orderby: 'update_time',
})

const orderOption = [
  {
    label: 'ID',
    value: 'id'
  },
  {
    label: '名称',
    value: 'name'
  },
  {
    label: '创建时间',
    value: 'create_time'
  },
  {
    label: '更新时间',
    value: 'update_time'
  }]

const getResource = () => {
  loadingBar.start()
  data.loading = true
  adminGetResource(queryData).then(res => {
    if (res.data.code === 200) {
      data.resourceData = res.data.data
      loadingBar.finish()
      data.loading = false
    } else if (res.data.code === 400) {
      message.error(res.data.msg)
      loadingBar.error()
      data.loading = false
    } else if (res.data.code === 401) {
      message.warning(res.data.msg)
      loadingBar.error()
      data.loading = false
    }
  }).catch(err => {
    message.error(err.message)
    loadingBar.error()
    data.loading = false
  })
}


getResource()

const linkTo = (id) => {
  router.push({
    name: 'resource',
    query: {
      resourceId: id
    }
  })
}
const editData = (item) => {
  data.editData = item
  data.showEditResource = true
}

const pagination = reactive({
  page: 1,
  pageSize: 10,
  showSizePicker: true,
  pageSizes: [10, 20, 50, 100],
  onUpdatePage: (page) => {
    pagination.page = page
    getResource()
  },
  onUpdatePageSize: (pageSize) => {
    pagination.pageSize = pageSize
    getResource()
  }
})

const deleteResource = (id) => {
  const m = modal.create({
    title: '删除资源',
    content: '确定删除该资源吗？',
    preset: 'dialog',
    positiveText: '确定',
    negativeText: '取消',
    onPositiveClick: () => {
      adminDeleteResource({resourceId: id}).then(res => {
        if (res.data.code === 200) {
          message.success(res.data.msg)
          getResource()
          m.destroy()
        } else if (res.data.code === 400) {
          message.error(res.data.msg)
          m.destroy()
        } else if (res.data.code === 401) {
          message.warning(res.data.msg)
          m.destroy()
        }
      }).catch(err => {
        message.error(err.message)
        m.destroy()
      })
    },
    onNegativeClick: () => {
      m.destroy()
    }
  })
}
</script>

<template>
  <n-layout>
    <n-layout-header style="padding: 24px">
      <n-page-header @back="router.push({name: 'adminHome'})">
        <template #header>
          <n-breadcrumb>
            <n-breadcrumb-item>
              <n-dropdown :options="menuOptions" @select="handleSelect">
                <div>
                  <n-icon :component="HomeOutlined" size="16"/>
                  首页
                </div>
              </n-dropdown>
            </n-breadcrumb-item>
            <n-breadcrumb-item>
              <n-icon :component="InboxOutlined" size="16"/>
              资源
            </n-breadcrumb-item>
          </n-breadcrumb>
        </template>
        <template #title>
          资源
        </template>
        <template #extra>
          <n-button secondary style="margin-right: 10px" type="info" @click="data.showAddResourceMultiple = true">
            批量添加
          </n-button>
          <n-button secondary type="primary" @click="data.showAddResource = true">添加资源</n-button>
        </template>
        <template #default>
          <n-grid :cols="4" :x-gap="24" :y-gap="24">
            <n-grid-item :span="1">
              <n-input v-model:value="queryData.kwd" placeholder="请输入资源名称" @update:value="getResource"></n-input>
            </n-grid-item>
            <n-grid-item :span="1" style="display: flex">
              <n-radio-group v-model:value="queryData.order" style="margin-right: 10px" @update:value="getResource">
                <n-radio-button value="asc">升序</n-radio-button>
                <n-radio-button value="desc">降序</n-radio-button>
              </n-radio-group>
              <n-select v-model:value="queryData.orderby" :options="orderOption" style="width: 100px"
                        @update:value="getResource"/>
            </n-grid-item>
          </n-grid>
        </template>
      </n-page-header>
    </n-layout-header>
    <n-spin :spinning="data.loading">
      <n-layout-content style="padding: 24px 0;height: 60vh">
        <n-grid v-if="data.resourceData.length > 0" :x-gap="24" :y-gap="24" cols="2 768:4">
          <n-grid-item
              v-for="item in data.resourceData.slice((pagination.page - 1) * pagination.pageSize, pagination.page * pagination.pageSize)"
              :key="item.id" :span="1">
            <n-card :bordered="false">
              <template #header>
                <n-text :ellipsis="true">
                  <n-gradient-text type="success">{{ item.name }}</n-gradient-text>
                </n-text>
              </template>
              <template #cover>
                <img :alt="item.cover" :src="item.cover" height="160">
              </template>
              <template #default>
                <div style="overflow: hidden;display: flex;margin-bottom: 5px">
                  <n-tag size="small" style="margin-right: 5px" type="success">
                    <template #icon>
                      <n-icon :component="FolderOutlined" size="16"/>
                    </template>
                    {{ item.category.name }}
                  </n-tag>
                  <n-tag v-for="tag in item.tags" size="small" style="margin-right: 5px" type="info">
                    <template #icon>
                      <n-icon :component="TagOutlined" size="16"/>
                    </template>
                    {{ tag.name }}
                  </n-tag>
                </div>
                <div style="margin-top: 5px">
                  <n-ellipsis :tooltip="false" line-clamp="2">{{ item.description }}</n-ellipsis>
                </div>
              </template>
              <template #footer>
                <div style="display: flex;justify-content: flex-end">
                  <n-button secondary size="small" style="margin-right: 10px" type="primary"
                            @click="linkTo(item.id)">查看
                  </n-button>
                  <n-button secondary size="small" style="margin-right: 10px" type="info"
                            @click="editData(item)">
                    修改
                  </n-button>
                  <n-button secondary size="small" type="error"
                            @click="deleteResource(item.id)">
                    删除
                  </n-button>
                </div>
              </template>
            </n-card>
          </n-grid-item>
        </n-grid>
        <n-empty v-else description="暂无数据" style="margin-top: 25%"/>
      </n-layout-content>
    </n-spin>
    <n-layout-footer style="padding: 24px">
      <n-pagination v-model:page="pagination.page" v-model:page-size="pagination.pageSize"
                    :item-count="data.resourceData.length" :page-sizes="pagination.pageSizes"
                    :show-size-picker="pagination.showSizePicker" :show-total="true"
                    @update-page-size="pagination.onUpdatePageSize" @update:page="pagination.onUpdatePage"/>
    </n-layout-footer>
  </n-layout>
  <AddResourceMultiple :show="data.showAddResourceMultiple" @finish="getResource"
                       @update:show="data.showAddResourceMultiple = $event"/>
  <AddResource :show="data.showAddResource" @finish="getResource" @update:show="data.showAddResource = $event"/>
  <EditResource :data="data.editData" :show="data.showEditResource" @finish="getResource"
                @update:show="data.showEditResource = $event"/>
</template>

